import { fetchGet } from 'background/api/rest/v2';

export default async function getAccess(accessToken) {
  let res = null;
  try {
    res = await fetchGet(
      '/access',
      {
        headers: {
          'X-Additor-Access': accessToken,
        },
      },
      false,
    );
  } catch (e) {
    console.error(e);
    // 401인 경우 Sentry에 보내지 않음(따로 catch함) - 뒤에 이어서 로직을 진행해야 하기 때문
  }
  return res;
}
