import * as Sentry from '@sentry/browser';
import _ from 'lodash';
import GA from 'background/tracking/ga';
import {
  createBrowserActionContextMenu,
  createPageContextMenu,
} from 'background/chrome/contextMenu';

import onInstalled from './eventHandler/onInstalled';
import onMessage from './eventHandler/onMessage';
import onMessageExternal from './eventHandler/onMessageExternal';
import onRestartRequired from './eventHandler/onRestartRequired';
import onUpdateAvailable from './eventHandler/onUpdateAvailable';
import onCommand from './eventHandler/onCommand';
import onClickBrowserActionIcon from './eventHandler/onClickBrowserActionIcon';
import onCookieChanged from './eventHandler/onCookieChanged';
import onTabRemoved from './eventHandler/onTabRemoved';
import {
  loadImmediatelyOpenWidget,
  saveImmediatelyOpenWidget,
} from './stroage/temp';
import {
  fetchAndSetWorkspaces,
  loadCurrentWorkspaceId,
  getCurrentWorkspaceId,
  switchFirstWorkspace,
  switchWorkspace,
} from './stroage/workspace';

Sentry.init({
  dsn: process.env.SENTRY_DSN,
  maxBreadcrumbs: 50,
  beforeSend(event, hint) {
    const error = hint.originalException;
    if (
      error?.message?.match(/UnhandledRejection/i) ||
      error?.message?.match(/Error: undefined/i)
    ) {
      // 비정상적으로 무수히 반복되는 특정 에러를 필터링 한다.
      return null;
    }
    // if (event.exception) {
    //   event.exception.values[0].stacktrace.frames.forEach(frame => {
    //     frame.filename = frame.filename.replace(
    //       'chrome-extension://jcigifkjfobfhdcddlfhnmleojpmolfp',
    //       './extension',
    //     );
    //   });
    // }
    return event;
  },
});

GA.init();

async function start() {
  chrome.runtime.onInstalled.addListener(onInstalled);
  chrome.runtime.onMessage.addListener(onMessage);
  chrome.runtime.onMessageExternal.addListener(onMessageExternal);
  chrome.runtime.onRestartRequired.addListener(onRestartRequired);
  chrome.runtime.onUpdateAvailable.addListener(onUpdateAvailable);
  chrome.commands.onCommand.addListener(_.throttle(onCommand, 1000));
  chrome.cookies.onChanged.addListener(onCookieChanged);
  chrome.tabs.onRemoved.addListener(onTabRemoved);
  chrome.browserAction.onClicked.addListener(
    _.throttle(onClickBrowserActionIcon, 1000),
  );
  myUndefinedFn();

  createBrowserActionContextMenu();
  createPageContextMenu();
  await fetchAndSetWorkspaces();
  await loadCurrentWorkspaceId();
  const currentWorkspaceId = getCurrentWorkspaceId();
  if (currentWorkspaceId) {
    await switchWorkspace(currentWorkspaceId);
  } else {
    await switchFirstWorkspace();
  }

  /**
   * NOTE: contentScript 에서 에러가 나서 backgroundScript refresh 요청을 한 경우,
   *       backgroundScript 에서 refresh 하기 전에, refresh 를 다시 해주어야 하는 tab 의 정보를
   *       local storage 에 저장한다.
   *       backgroundScript 가 refresh 된 후 새로 시작할 때 저장되어있는 tab 정보가 있으면
   *       해당 탭에서 browserAction 버튼을 클릭한 것과 동일한 동작을 해주고 저장되어있던 tab 정보를 제거한다.
   * */
  const loadImmediatelyOpenWidgetTab = await loadImmediatelyOpenWidget();
  if (loadImmediatelyOpenWidgetTab) {
    onClickBrowserActionIcon(loadImmediatelyOpenWidgetTab);
    saveImmediatelyOpenWidget(null);
  }
}

start();
