import * as Elements from '@additor/elements';
import _ from 'lodash';
import { getTabById, queryTabs } from 'background/chrome/tab';

const EXTERNAL_MESSAGE_TYPES = {
  RECEIVE_WEB_CARD: 'RECEIVE_WEB_CARD',
};

export default function onMessageExternal(message, sender, respond) {
  const { tab } = sender;
  const { data } = message;
  const actions = {
    [EXTERNAL_MESSAGE_TYPES.RECEIVE_WEB_CARD]() {
      const { card: serializedCard, highlights: serializedHighlights } = data;
      const card = new Elements.Card(JSON.parse(serializedCard));
      const cardUri = decodeURIComponent(_.get(card, 'resource.uri'));
      if (!cardUri) return;
      /*
       * 전달된 Card에 해당하는 탭을 찾은 뒤,
       * 해당 탭의 ID를 부여하여 Card및 Highlight 데이터를 전달한다.
       */
      queryTabs({ windowId: tab.windowId }, tabs => {
        const targetTabs = _.filter(
          tabs,
          eachTab => eachTab.url.split('#')[0] === cardUri.split('#')[0],
        );
        if (targetTabs.length > 1) {
          // 같은 url의 탭이 두개이상 존재할 경우 가장 최근에 열린 탭에 카드와 하이라이트를 넘겨준다.
          // 가장 최근에 열린 탭인지는 id의 크기로 비교한다. (id가 클수록 최근에 열린 탭으로 본다.)
          targetTabs.sort((a, b) => b.id - a.id);
        }
        const targetTab = targetTabs[0];
        if (!targetTab) return;

        const targetTabId = targetTab.id;

        /*
         * 데이터 전달 로직.
         */
        const dispatchDataToTab = () => {
          // TODO: 해당 탭에 애딧을 전달하는 로직 작성
        };

        /*
         * 페이지가 로딩 중인 경우,
         * Content Script 의 LifeCycle 로 인해 데이터 처리가 제대로 되지 않을 수 있기 때문에
         * 탭이 로딩 완료될 때까지 대기한 후 데이터를 전달한다.
         */
        if (targetTab.status === 'complete') {
          dispatchDataToTab();
        } else {
          const intervalId = setInterval(() => {
            getTabById(targetTabId, selectedTab => {
              if (chrome.runtime.lastError) {
                // 에러 발생시 intervalId 를 초기화한다.
                clearInterval(intervalId);
              } else if (selectedTab.status === 'complete') {
                // Interval 반복 취소.
                clearInterval(intervalId);
                dispatchDataToTab();
              }
            });
            // 탭이 로딩 완료됬는지를 파악하기 위하여, 0.5초마다 탭을 조회한다.
          }, 500);
        }
      });
    },
  };

  const actionFunc = actions[message.type];
  if (_.isFunction(actionFunc)) {
    try {
      actionFunc();
    } catch (e) {
      // error
    }
  }
}
